.banner{
    width: 100%;
    background-image: url('./assets/img2.jpg');
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
}
.banner-image{
    background: rgba(0,0,0,0.37);
    width: 100%;
    height: 500px;
    padding-top: 9%;
    padding-bottom: 9%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.header-text{
    font-family: trendsetter;
    font-style: italic;
    color: #fff;
    font-size: 60px;
    font-weight: 500;
    line-height: 66px;
}

.inform-text{
    color:#fff;
    margin-top: 16px;
    max-width: 500px;
}